<h2 id="java发展简史" tabindex="-1"><a class="header-anchor" href="#java发展简史" aria-hidden="true">#</a> Java发展简史</h2>
<p>Java之父 - <strong>James Gosling</strong></p>
<p>Java的历史要追溯到<strong>1991年</strong>， Java语言诞生于 - <strong>1995年5月23日</strong>，</p>
<p><strong>1996年初</strong> Sun发布了Java的第一个版本</p>
<p><strong>2009年</strong>Sun被Oracle收购</p>
