<h2 id="java技术体系" tabindex="-1"><a class="header-anchor" href="#java技术体系" aria-hidden="true">#</a> Java技术体系</h2>
<ul>
<li>Java程序设计语言</li>
<li>各种硬件平台上的Java虚拟机</li>
<li>class文件格式</li>
<li>Java API类库</li>
<li>来自商业机构和开源社区的第三方Java类库</li>
</ul>
<p>Java技术体系可以分为四个平台</p>
<ul>
<li>Java Card</li>
<li>Java ME</li>
<li>Java SE</li>
<li>Java EE</li>
</ul>
<h2 id="jdk-vs-jre" tabindex="-1"><a class="header-anchor" href="#jdk-vs-jre" aria-hidden="true">#</a> JDK VS JRE</h2>
<p><strong>JDK</strong></p>
<p>Java程序设计语言，Java虚拟机，Java API类库这三部分统称为JDK(Java Development Kit), JDK是用于支持Java程序开发的最小环境。</p>
<p><strong>JRE</strong></p>
<p>Java API类库中的JavaSE API子集和Java虚拟机这两部分统称为JRE(Java Runtime Environment)，JRE是支持Java程序运行的标准环境。</p>
<h2 id="openjdk-vs-jdk" tabindex="-1"><a class="header-anchor" href="#openjdk-vs-jdk" aria-hidden="true">#</a> OpenJDK VS JDK</h2>
<p>OpenJDK是Sun在<strong>2006年末把Java开源而形成的项目</strong>。</p>
<p>Oracle JDK采用了商业实现，而OpenJDK使用是开源的。OpenJDK7和OracleJDK7在程序上是非常接近的，两者使用了大量相同的代码。</p>
